@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
}
h2 {
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 300;
  text-align: center;
}

.btn {
  margin-top: 4vh;
  padding: 1vh 4vw;
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 400;
}
.btn.wide {
  min-width: 20vw;
}
.fullwidth {
  width: 100%;
}

form {
  margin: auto;
}
input {
  margin-top: 1em;
}

li {
  list-style: none;
  list-style-position: inside;
  margin-left: 0;
}

.App {
  width: 100%;
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.width-100 {
  width: 100vw;
}
.width-90 {
  width: 90vw;
}
.width-80 {
  width: 80vw;
}
.width-70 {
  width: 70vw;
}
.width-60 {
  width: 60vw;
}
.width-50 {
  width: 50vw;
}
.width-40 {
  width: 40vw;
}
.width-30 {
  width: 30vw;
}

@media screen and (min-width: 1000px) {
  .width-40-on-desktop {
    width: 40vw;
  }
}
@media screen and (max-width: 1000px) {
  .width-40-on-desktop {
    width: 60vw;
  }
}

.align-center {
  text-align: center;
}
.align-left {
  text-align: left;
}

.flex-column {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.flex-column-wrap {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

.flex-row-wrap {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.flex-row {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
}

.record-button {
  margin-top: 2vh;
}

.login-link-container {
/*  margin-top: 0.5em;*/
}

.get-started {
  margin-top: 4vh;
  padding: 1vh 10vh;
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 400;
}

.mood-button {
  display: inline-block;
  margin-top: 1vh;
  background-color: rgba(0,0,0,0);
  border: none;
  font-size: calc(1.5rem + 1.5vw);
  font-family: emoji;
}
.mood-button.selected {
  font-size: calc(1.5rem + 1.5vw) !important;
}

.mood-chooser hr {
  width: 100%;
  visibility: hidden;
}

.message-input {
    align-items: center;
    width: 80%;
    height: 200px;
}

.messages-view-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.messages-view {
    width: 80vw;
    height: 60vh;
    background-color: white;
    border-radius: 10px;
    overflow: scroll;
    max-height: 70vh;
}

.user-message {
    color: grey;
    margin-left: 20px;
    margin-top: 20px;
}

.bot-message {
    color: rgb(201, 36, 36);
    margin-left: 20px;
}

.diary-container {
  padding: 20px;
  outline: 2px solid #eee;
  border-radius: 8%;
  max-height: 70vh;
  overflow: scroll;
}
.diary-container hr {
  border: 1.5px solid #eee;
}

.mood {
  margin-right: 2vw;
  font-size: calc(1.5rem + 1.5vw);
  font-family: emoji;
}

.message, .conversation {
  font-size: calc(0.3rem + 1.5vw);
}

